import React from 'react'
import jsonData from '../data/dictionary';
import { cloneDeep } from 'lodash';

class Dictionary extends React.Component {
    constructor() {
        super();
        this.state = { dictionary: '' };
    }

    componentDidMount() {
        const text = jsonData;
        this.setState({ dictionary: text });
    }

    render() {
        const { dictionary } = this.state;
        console.log(dictionary);
        return (
            <div>
                <ul>
                {Object.keys(dictionary).map(s => (<li key={s}>{s + " " + dictionary[s].type + " ~ " + dictionary[s].translation}</li>))}    
                </ul>           
            </div>
        );
    }
}
export default Dictionary